import {defineStore} from "pinia";
import {io} from "socket.io-client";
import {computed, onMounted, ref} from "vue";
import ServerActions from "@/consts/server-actions";
import {User} from "@/consts/models";
import ClientActions from "@/consts/client-actions";
import {GameMainInfo} from "@/consts/other";
import {useTimeMachine} from "@/composible/time-machine";
import TelegramWebApp from "@/telegram/TelegramWebApp";

export const useGameStore = defineStore('game-store', () => {

	const timeMachine = useTimeMachine();

	let socket = io(
		<string>process.env.VUE_APP_SOCKET_URL,
		{
			path : <string>process.env.VUE_APP_SOCKET_PATH,
			autoConnect : false,
			transports : ['websocket']
		}
	);

	let connected = ref<boolean>(false);
	let wasConnected = ref<boolean>(false);
	let ready = ref<boolean>(false);
	let wasReady = ref<boolean>(false);
	let authorized = ref<boolean>(false);

	let user = ref<User | null>(null);
	let game = ref<GameMainInfo | null>(null);

	socket.on('connect', () => {
		connected.value = true;
		wasConnected.value = true;

		let initData = (TelegramWebApp.authorized ? TelegramWebApp.app!.initData : process.env.VUE_APP_FAKE_USER);

		socket.emit(ClientActions.AUTH, {
			initData
		});

	});

	socket.on(ServerActions.ON_AUTH_SUCCESS, (json : any) => {

		ready.value = true;
		wasReady.value = true;

		authorized.value = true;
		user.value = json.user;
		game.value = json.game;

	});

	socket.on(ServerActions.ON_USER_UPDATE, (json : any) => {
		user.value = json.user;
	});

	socket.on('error', json => {
		console.log(json);
	});

	socket.on('disconnect', () => {
		connected.value = false;
		ready.value = false;
		authorized.value = false;
		user.value = null;
		game.value = null;
	});

	timeMachine.interval(() => {

		if(authorized.value){
			socket.emit(ClientActions.USER_INFO);
		}

	}, 100);

	socket.connect();

	return {
		socket,
		connected,
		authorized,
		wasConnected,
		ready,
		game,
		user
	}

});