<script lang="ts" setup>

import {useGameStore} from "@/stores/game-store";
import ClientActions from "@/consts/client-actions";
import {computed, onBeforeUnmount, onMounted, ref, watch} from "vue";
import VSpinePlayer from "@/components/Utility/SpinePlayer/VSpinePlayer.vue";
import {useDebounce} from "@/composible/debounce";
import {useThrottler} from "@/composible/throttler";
import ServerActions from "@/consts/server-actions";
import Random from "@/modules/Random";
import VTap from "@/components/Game/VTap.vue";
import {useClickHints} from "@/composible/click-hints";
import {useTimeMachine} from "@/composible/time-machine";
import {useBackgroundStore} from "@/stores/background-store";
import VEnergy from "@/components/Game/VEnergy.vue";
import VBalance from "@/components/Game/VBalance.vue";

interface ExplosionElement{
	x : number,
	y : number,
	id : number,
}

const gameStore = useGameStore();
const clickHints = useClickHints();
const timeMachine = useTimeMachine();
const backgroundStore = useBackgroundStore();

let explosionElements   = ref<ExplosionElement[]>([]);

let cachedBalance = ref<number>(gameStore.user!.balance),
	cachedEnergy = ref<number>(gameStore.user!.energy),
	cachedEnergyCharges = ref<number>(gameStore.user!.energy_charges);

let canTouch = computed<boolean>(() => cachedEnergy.value >= gameStore.game!.energy.spendRate);

let destroySpawnThrottler = useThrottler(1000),
	destroyClearDebounce = useDebounce(2000),
	explosionSpawnThrottler = useThrottler(800);

watch(() => gameStore.user!.balance, (v) => {
	cachedBalance.value = v;
})

watch(() => gameStore.user!.energy, (e) => {
	cachedEnergy.value = e;
});

watch(() => gameStore.user!.energy_charges, (e) => {
	cachedEnergyCharges.value = e;
})

function onGameClick(event : MouseEvent){

	if(!canTouch.value){
		return;
	}

	cachedEnergy.value -= gameStore.game!.energy.spendRate;
	cachedBalance.value += gameStore.game!.energy.rewardRate;

	gameStore.socket.emit(ClientActions.TAP);

	destroySpawnThrottler(() => {
		backgroundStore.addDestroyElement();
	});

	destroyClearDebounce(() => {
		backgroundStore.clearDestroyElements();
	});

	explosionSpawnThrottler(() => {

		let explosion = {
			x : event.clientX,
			y : event.clientY,
			id : Date.now()
		};

		explosionElements.value.push(explosion);

		timeMachine.timeout(() => {
			explosionElements.value.splice(
				explosionElements.value.indexOf(explosion), 1
			);
		}, 700);

	});

	clickHints.push(event.clientX - 10, event.clientY - 10, '+' + gameStore.game!.energy.rewardRate, 40);

}

function onServerAnswerTap(){


}

onMounted(() => {
	gameStore.socket.on(ServerActions.ON_TAP, onServerAnswerTap);
});

onBeforeUnmount(() => {
	gameStore.socket.off(ServerActions.ON_TAP, onServerAnswerTap);
});

</script>
<template>
	<main class="game">
		<div class="game__zone" @click.prevent="onGameClick"></div>
		<VEnergy @change="cachedEnergyCharges--" :value="cachedEnergy" :max="gameStore.user!.energy_max" :charges="cachedEnergyCharges" class="game__energy" />
		<VBalance :value="cachedBalance" class="game__balance" />
		<div class="game__taps">
			<div class="game__taps-item tap"
			     v-for="tap in explosionElements"
			     :key="tap.id"
			     :style="{ top : tap.y + 'px', left : tap.x + 'px' }">
			</div>
		</div>
	</main>
</template>
